<template>
	<nav class="nav">
		<div class="logo"> 
			<Icon type="ios-cart-outline" v-bind:size="46"></Icon>
			<span>braaow</span>
		</div>
		<div class="person-msg">
			<div class="person-img">QQ</div>
			<span class="person-name tongle">Kenny</span>
			<Icon class="unread" v-bind:size="32" type="ios-email-outline"></Icon>
			<span class="helper tongle">Help</span>
			<a href="#" class="login-btn">List Your Space</a>
		</div>
	</nav>		
</template>
<script>
	export default{
		name:"navbar"
	}
</script>
<style>
	nav.nav{
		background:#fff;
		line-height: 60px; 
		padding: 0 50px;
		font-size: 14px;
	}
	nav.nav::after{
		content: "";
		display: table;
		clear: both; 
	}
	.logo{
		float: left;
		color:#1B7A8A;
		font-size:24px;
		font-weight: 600;
	    display: flex;
    	align-items: center;
	} 
	.logo i{
		font-size: 40px;
	    font-weight: 600;
	    margin-right: 10px;
	}
	.person-msg{
		float: right;
		display: flex;
		align-items: center;
	}
	.person-img{
    	width: 32px;
		line-height: 32px;
	    border-radius: 50%;
	    background:rgba(27,122,138,.8); 
	    color: #fff;
	    text-align: center;
	    font-size: 16px;
	    cursor: pointer;
	    transition: all .3s;
	    
	}
	.person-img:hover{ 
		 background:#1B7A8A; 
	}
	.person-name,.helper{
		margin:0 30px 0 10px;  
		position: relative;
		cursor:pointer;
	}
	.tongle::after{
		content: "";
		display: block;
		width: 0;
		height: 0;
		border-left:4px solid transparent;
		border-right:4px solid transparent;
		border-top:6px solid rgba(80,90,92,.35);
		position: absolute; 
		right:-12px;
		top:27px;
		transition: all .3s;
	}
	.tongle:hover::after{
		border-top:5px solid rgba(80,90,92,1);
	}
	.login-btn{
		color:rgba(255,255,255,.8);
		background: #1B7A8A;
		line-height: 36px;
		padding: 0 12px;
		font-weight: 600;
		border-radius: 2px;
		box-shadow: 2px 4px 8px 0 rgb(255,255,255);
		transition: all .3s;
	}
	.login-btn:hover{
		color:#fff;
	}
	.unread{
		position: relative;
		margin:0 10px;
	}
	.unread::after{
		content: "2";
		display: block;
		position: absolute;
		top:-2px;
		right:-10px;
		font-size:10px;
		background:#FF5A5F; 
		color:#fff;
		font-weight: 600;
		width: 18px;
		line-height:18px; 
		border-radius: 50%;
		text-align: center;
	}
</style>


